import { request, type BaseResponse } from "@/utils/request";

/** AI  POST /api/mini/chapter_demo/random */
export async function miniRandom(
  body: API.ChapterDemoRequest,
  options?: BaseResponse
) {
  return request<string>("/api/mini/chapter_demo/random", {
    method: "POST",
    headers: {
      "Content-Type": "application/json",
    },
    data: body,
    ...(options || {}),
  });
}
