import { request, type BaseResponse } from "@/utils/request";

/**   GET /api/mini/verification/list */
export async function miniList(
  // Param (bodyswagger)
  params: API.miniListParams,
  options?: BaseResponse
) {
  return request<string>("/api/mini/verification/list", {
    method: "GET",
    params: {
      ...params,
    },
    ...(options || {}),
  });
}

/**   GET /api/mini/verification/query */
export async function miniQuery(
  // Param (bodyswagger)
  params: API.miniQueryParams,
  options?: BaseResponse
) {
  return request<string>("/api/mini/verification/query", {
    method: "GET",
    params: {
      ...params,
    },
    ...(options || {}),
  });
}
