import { request, type BaseResponse } from "@/utils/request";

/**   GET /api/admin/event */
export async function index(
  // Param (bodyswagger)
  params: API.indexParams,
  options?: BaseResponse
) {
  return request<string>("/api/admin/event", {
    method: "GET",
    params: {
      ...params,
    },
    ...(options || {}),
  });
}

/**   POST /api/admin/event */
export async function store(body: API.EventRequest, options?: BaseResponse) {
  return request<string>("/api/admin/event", {
    method: "POST",
    headers: {
      "Content-Type": "application/json",
    },
    data: body,
    ...(options || { successMsg: "" }),
  });
}

