import { request, type BaseResponse } from "@/utils/request";

/**   GET /api/admin/coupon_order */
export async function index(
  // Param (bodyswagger)
  params: API.indexParams,
  options?: BaseResponse
) {
  return request<string>("/api/admin/coupon_order", {
    method: "GET",
    params: {
      ...params,
    },
    ...(options || {}),
  });
}

/**   POST /api/mini/coupon_order */
export async function miniStore(
  body: API.CouponCourseRequest,
  options?: BaseResponse
) {
  return request<string>("/api/mini/coupon_order", {
    method: "POST",
    headers: {
      "Content-Type": "application/json",
    },
    data: body,
    ...(options || { successMsg: "" }),
  });
}

/**   GET /api/mini/coupon_order/${param0} */
export async function miniShow(
  // Param (bodyswagger)
  params: API.miniShowParams,
  options?: BaseResponse
) {
  const { id: param0, ...queryParams } = params;
  return request<string>(`/api/mini/coupon_order/${param0}`, {
    method: "GET",
    params: { ...queryParams },
    ...(options || { successMsg: "" }),
  });
}
