import { request, type BaseResponse } from "@/utils/request";

/**   POST /api/admin/user/coupon_makeup */
export async function makeup(body: API.MakeupRequest, options?: BaseResponse) {
  return request<string>("/api/admin/user/coupon_makeup", {
    method: "POST",
    headers: {
      "Content-Type": "application/json",
    },
    data: body,
    ...(options || {}),
  });
}

/**   GET /api/mini/coupon_mine */
export async function miniMine(options?: BaseResponse) {
  return request<string>("/api/mini/coupon_mine", {
    method: "GET",
    ...(options || {}),
  });
}

/**   GET /api/mini/user_coupon_template/${param0} */
export async function miniShow(
  // Param (bodyswagger)
  params: API.miniShowParams,
  options?: BaseResponse
) {
  const { id: param0, ...queryParams } = params;
  return request<string>(`/api/mini/user_coupon_template/${param0}`, {
    method: "GET",
    params: { ...queryParams },
    ...(options || {}),
  });
}

