import { request, type BaseResponse } from "@/utils/request";

/**   GET /api/admin/course */
export async function index(
  // Param (bodyswagger)
  params: API.indexParams,
  options?: BaseResponse
) {
  return request<string>("/api/admin/course", {
    method: "GET",
    params: {
      ...params,
    },
    ...(options || {}),
  });
}

/**   POST /api/admin/course */
export async function store(body: API.CourseRequest, options?: BaseResponse) {
  return request<string>("/api/admin/course", {
    method: "POST",
    headers: {
      "Content-Type": "application/json",
    },
    data: body,
    ...(options || { successMsg: "" }),
  });
}

