/**
 *  @umijs/openapi  openapi.config.ts 
 * */

import { request, type BaseResponse } from "@/utils/request";

/**   POST /api/mini/order */
export async function miniStore(
  body: API.OrderRequest,
  options?: BaseResponse
) {
  return request<string>("/api/mini/order", {
    method: "POST",
    headers: {
      "Content-Type": "application/json",
    },
    data: body,
    ...(options || { successMsg: "" }),
  });
}
