/**   GET /api/admin/schedule */
export async function index(
  // Param (bodyswagger)
  params: API.indexParams,
  options?: BaseResponse
) {
  return request<string>("/api/admin/schedule", {
    method: "GET",
    params: {
      ...params,
    },
    ...(options || {}),
  });
}

/**   POST /api/admin/schedule */
