/**
 *  @umijs/openapi  openapi.config.ts 
 * */

import { request, type BaseResponse } from "@/utils/request";

/** NearBy site . POST /api/admin/venue/nearby */
export async function venueNearBy(body: any, options?: BaseResponse) {
  return request<any>("/api/admin/venue/nearby", {
    method: "POST",
    headers: {
      "Content-Type": "application/json",
    },
    data: body,
    ...(options || {}),
  });
}
