/**
 *  @umijs/openapi  openapi.config.ts 
 * */

import { request, type BaseResponse } from "@/utils/request";

/**   POST /api/admin/option/teacher */
export async function controllerTeacher(
  body: API.TeacherOptionRequest,
  options?: BaseResponse
) {
  return request<string>("/api/admin/option/teacher", {
    method: "POST",
    headers: {
      "Content-Type": "application/json",
    },
    data: body,
    ...(options || { successMsg: "" }),
  });
}
