import { request, type BaseResponse } from "@/utils/request";

/**   POST /api/admin/reservation */
export async function store(
  body: API.ReservationRequest,
  options?: BaseResponse
) {
  return request<string>("/api/admin/reservation", {
    method: "POST",
    headers: {
      "Content-Type": "application/json",
    },
    data: body,
    ...(options || { successMsg: "" }),
  });
}

