/**   GET /api/admin/team */
export async function index(
  // Param (bodyswagger)
  params: API.indexParams,
  options?: BaseResponse
) {
  return request<string>("/api/admin/team", {
    method: "GET",
    params: {
      ...params,
    },
    ...(options || {}),
  });
}

/**   POST /api/admin/team */
export async function store(body: API.TeamRequest, options?: BaseResponse) {
  return request<string>("/api/admin/team", {
    method: "POST",
    headers: {
      "Content-Type": "application/json",
    },
    data: body,
    ...(options || { successMsg: "" }),
  });
}

