/**   POST /api/admin/event_registration */
export async function store(
  body: API.EventRegistrationRequest,
  options?: BaseResponse
) {
  return request<string>("/api/admin/event_registration", {
    method: "POST",
    headers: {
      "Content-Type": "application/json",
    },
    data: body,
    ...(options || { successMsg: "" }),
  });
}

