import { request, type BaseResponse } from "@/utils/request";

/**   GET /api/mini/main */
export async function miniHomeMain(options?: BaseResponse) {
  return request<string>("/api/mini/main", {
    method: "GET",
    ...(options || {}),
  });
}

/**   GET /api/mini/secondary */
export async function miniHomeSecondary(options?: BaseResponse) {
  return request<string>("/api/mini/secondary", {
    method: "GET",
    ...(options || {}),
  });
}
