/**   GET /api/admin/coach */
export async function index(
  // Param (bodyswagger)
  params: API.indexParams,
  options?: BaseResponse
) {
  return request<string>("/api/admin/coach", {
    method: "GET",
    params: {
      ...params,
    },
    ...(options || {}),
  });
}

/**   POST /api/admin/coach */
export async function store(body: API.CoachRequest, options?: BaseResponse) {
  return request<string>("/api/admin/coach", {
    method: "POST",
    headers: {
      "Content-Type": "application/json",
    },
    data: body,
    ...(options || { successMsg: "" }),
  });
}

