/**
 *  @umijs/openapi  openapi.config.ts 
 * */

import { request, type BaseResponse } from "@/utils/request";

/**   POST /api/mini/scan */
export async function scan(body: API.ScanRequest, options?: BaseResponse) {
  return request<string>("/api/mini/scan", {
    method: "POST",
    headers: {
      "Content-Type": "application/json",
    },
    data: body,
    ...(options || { successMsg: "" }),
  });
}
