import { request, type BaseResponse } from "@/utils/request";

/**   GET /api/admin/venue */
export async function index(
  // Param (bodyswagger)
  params: API.indexParams,
  options?: BaseResponse
) {
  return request<string>("/api/admin/venue", {
    method: "GET",
    params: {
      ...params,
    },
    ...(options || {}),
  });
}

/**   POST /api/admin/venue */
export async function store(body: API.VenueRequest, options?: BaseResponse) {
  return request<string>("/api/admin/venue", {
    method: "POST",
    headers: {
      "Content-Type": "application/json",
    },
    data: body,
    ...(options || { successMsg: "" }),
  });
}

