/**
 *  @umijs/openapi  openapi.config.ts 
 * */

import { request, type BaseResponse } from "@/utils/request";

/**   POST /api/mini/update_state */
export async function miniUpdateState(
  body: API.UpdateStateRequest,
  options?: BaseResponse
) {
  return request<string>("/api/mini/update_state", {
    method: "POST",
    headers: {
      "Content-Type": "application/json",
    },
    data: body,
    ...(options || {}),
  });
}
