/**
 *  @umijs/openapi  openapi.config.ts 
 * */

import { request, type BaseResponse } from "@/utils/request";

/**   POST /api/mini/pay */
export async function miniWechatPay(
  body: Record<string, any>,
  options?: BaseResponse
) {
  return request<string>("/api/mini/pay", {
    method: "POST",
    headers: {
      "Content-Type": "application/json",
    },
    data: body,
    ...(options || {}),
  });
}

/**   POST /api/mini/ticket_pay */
export async function miniWechatTicketPay(
  body: Record<string, any>,
  options?: BaseResponse
) {
  return request<string>("/api/mini/ticket_pay", {
    method: "POST",
    headers: {
      "Content-Type": "application/json",
    },
    data: body,
    ...(options || {}),
  });
}
